import React, { Component } from 'react'
import { Form, Button, Icon, Modal } from 'semantic-ui-react'

class LoginModal extends Component {
  state = { 
    username: "",
    password: "",
    server: "http://chat.cs291.com",
  }

  close = () => {
    if(!(this.state.username === "" || this.state.password === "")){
      if(this.props.login(this.state.server, this.state.username, this.state.password)){
        this.props.closeModal();
      }
    }else{
      alert("Please enter a valid username and password");
    }
  }

  handleChangeServer= (event) => {
    this.setState({server: event.target.value});
  }
  handleChangeUsername = (event) => {
    this.setState({username: event.target.value});
  }
  handleChangePassword = (event) => {
    this.setState({password: event.target.value});
  }

  render() {
    return (
      <Modal
        open={this.props.isVisible}
        onOpen={this.props.openModal}
        trigger={
          <Button color={this.props.connected ? 'green' : 'red'} icon>
            Login <Icon name='check' />
          </Button>
        }
      >
        <Modal.Header>Login</Modal.Header>
        <Modal.Content>
          <Form>
            <Form.Field 
              required             
            >
              <input placeholder='Server Url' onChange={this.handleChangeServer} value={this.state.server}/>
            </Form.Field>
            <Form.Field 
              required             
            >
              <input placeholder='Username' onChange={this.handleChangeUsername} value={this.state.username}/>
            </Form.Field>
            <Form.Field 
            required
            >
              <input type="password" placeholder='Password' onChange={this.handleChangePassword} value={this.state.password}/>
            </Form.Field>
          </Form>
        </Modal.Content>
        <Modal.Actions>
          <Button icon='check' content='Login' onClick={this.close} />
        </Modal.Actions>
      </Modal>
    )
  }
}

export default LoginModal;