import React from 'react';
import UserList from './Components/UserList';
import MessageList from './Components/MessageList'
import './App.css';
import { Segment, Grid, Form } from 'semantic-ui-react';
import LoginModal from  './Components/LoginModal';
import axios from 'axios';

class App extends React.Component{
  state = {
    userList: new Set(),
    eventList: [],
    username: "",
    password: "",
    server: "",
    connection:  null,
    showModal: true,
    message: "",
    token: "",
  }

  openModal = () =>{
    this.setState({showModal: true});
  }
  closeModal = () =>{
    this.setState({showModal: false});
  }

  login = async (server,  username, password) => {
    if(this.state.server === server && 
      this.state.username === username &&
      this.state.password === password){
        return true;
    }

    if(this.state.connection !== null){
      this.state.connection.close();
    }

    this.setState({
      userList: new Set(),
      eventList: [],
      username: "",
      password: "",
      server: "",
      connection:  null,
      showModal: true,
      message: "",
      token: "",
    });
  
    this.setState({
      server: server,
      username: username, 
      password: password,
    });

    const form = new FormData();
    form.set('username', username);
    form.set('password', password);

    const response = await axios.post(
      server + '/login',
      form,
      {headers: {}},
    );

    if(response.status  !== 201){
      this.setState({
        token: "",
        connection: null,
      })
      alert("Issue connecting to server");
      return false;
    }
    this.setState({token: response.data.token});
    this.getConnection(response.data.token);
    return true;
  }

  addEvent = (type, event) => {
    this.setState({
      eventList: this.state.eventList.concat([[type, event]]),
    });
  }

  getConnection = async  (token) => {
    const eventSource = new EventSource(this.state.server + '/stream/' + token);

    this.setState({connection: eventSource});
    eventSource.addEventListener('Disconnect', (e) =>{
      this.state.connection.close();
      alert("Disconnect: You have logged in from another location.");
      this.setState({
        eventList: [],
        userList: new Set(),
        username: "",
        password: "",
        server: "",
        connection:  null,
        showModal: true,
        message: "",
        token: "",
      });
      this.openModal();
      this.render();
    });

    eventSource.addEventListener('Join', (e) =>{
      this.addEvent('Join', e);
      this.setState({
        userList: this.state.userList.add(JSON.parse(e.data).user),
      });
      this.render();
    });

    eventSource.addEventListener('Message', (e) =>{
      this.addEvent('Message', e);
      this.render();
    });

    eventSource.addEventListener('Part', (e) =>{
      this.addEvent('Part', e);
      let ev = JSON.parse(e.data);
      this.state.userList.delete(ev.user);
      this.setState({userList: this.state.userList});
      this.render();
    });
    
    eventSource.addEventListener('ServerStatus', (e) =>{
      this.addEvent('ServerStatus', e);
      this.render();
    });

    eventSource.addEventListener('Users', (e) =>{
      JSON.parse(e.data).users.forEach( (item) => {
        this.setState({userList: this.state.userList.add(item)});
      });
      this.render();
    });
  }

  updateMessage= (event) => {
    this.setState({message: event.target.value});
  }

  sendMessage = (event) => {
    if(event.keyCode === 13 && this.state.message !== "" && this.state.token !==  "" && this.state.server !== ""){
      let form = new FormData();
      form.append("message", this.state.message);
      axios.post(this.state.server + '/message', 
      form, {
        headers: {
          'Authorization': "Bearer " + this.state.token,
        },
      },);
      this.setState({message: ""});
    }
  }

  render(){
    console.log(this.state.userList)
    return (
      <div className="App" style={{maxHeight: '100vh'}}>
        <Grid celled style={{maxHeight: '100vh'}}>
          <Grid columns={2} divided stretched style={{minHeight: '100vh', maxHeight: '100vh'}}>
            <Grid.Row style={{maxHeight: '100vh'}}>
              <Grid.Column width={3} style={{maxHeight: '100vh'}}>
                <Grid columns={1} padded>
                  <Grid.Row style={{height: '10%'}}>
                    <LoginModal 
                    openModal={this.openModal} 
                    closeModal={this.closeModal} 
                    login={this.login} 
                    isVisible={this.state.showModal}
                    connected={this.state.connection != null}/>
                  </Grid.Row>
                  <Grid.Row style={{height: '80%', paddingTop: '3'}}>
                    <UserList userList={this.state.userList}/>
                  </Grid.Row>
                </Grid>
              </Grid.Column>
              <Grid.Column width={13} style={{maxHeight: '100vh'}}>
                <Grid.Row stretched style={{height: '75vh'}}>
                  <Segment style={{background: "gray" , overflowY: "scroll" , height: '75vh'}}>
                    <MessageList messageList={this.state.eventList} />
                  </Segment>
                </Grid.Row>
                <Grid.Row style={{height: '5vh'}}>
                  <Form>
                    <Form.Field>
                      <input placeholder='Send a message' value={this.state.message} onChange={this.updateMessage} onKeyDown={this.sendMessage}/>
                    </Form.Field>
                  </Form>
                </Grid.Row>
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </Grid>
      </div>
    );
  }
}

export default App;
