import React from 'react';
import {List} from 'semantic-ui-react'

class MessageItem extends React.Component {
  state ={
    create: "",
    type: "",
    user: "",
  }

  dateFormat = (time) =>{
    console.log("TIME" + time);
    let date = new Date(time * 1000);
    return date.toLocaleDateString("en-US") + " " + date.toLocaleTimeString("en-US");
  }

  componentDidMount(){
    this.setState({
      create: this.dateFormat(this.props.event['created']),
      type: this.props.type === "Message" ? "" : this.props.type.toUpperCase() + ":",
      user: this.props.event.hasOwnProperty('user') ? " (" + this.props.event.user + ")" : "",
      message: this.props.event.hasOwnProperty('message') ? " " + this.props.event.message : "",
      status: this.props.event.hasOwnProperty('status') ? " " +  this.props.event.status : "",
    });
  }

  render() {
    return (
      <div className="UserItem">
        <List.Item>{this.state.create + " " + this.state.type +  this.state.user + this.state.message + this.state.status}</List.Item>
      </div>
    );
  }
}

export default MessageItem;
